const getMyFriends = function(state){
    return state.friends
}
const getMyFoes = function(state){
    return state.foes
} 
const getMyTurn = function(state){
    return state.myturn
}

const getMyOpponent = function(state){
    return state.opponent
}

const getTargetEnemyStatus = function(state){
    return state.targetEnemyStatus
}

const getTargetAllyStatus = function(state){
    return state.targetAllyStatus
}

const getEnemyCharacterEffectList = function(state){
    return characterIndex => {
        return state.foes[characterIndex].effectList
    }
}

const getSelectedSkill = function(state){
    return state.selectedSkill
}

const getSkillQueue = function(state){
    return state.skillQueue
}

const getSkillQueueFilter = function(state){
    return id => {
        return state.skillQueue.filter(target => {
            for(const i in target.targets){
                if(target.targets[i].target.includes(id)) return true
            }
        })
    }
}

export default {
    getMyFoes,
    getMyFriends,
    getMyTurn,
    getMyOpponent,
    getTargetEnemyStatus,
    getTargetAllyStatus,
    getEnemyCharacterEffectList,
    getSelectedSkill,
    getSkillQueue,
    getSkillQueueFilter
}